import React from 'react';
import { Link } from 'gatsby';
import Layout from '@common/layout';
import Navbar from '@common/navbar';
import Footer from '@common/footer';
import Team from '@sections/Team';
import Clients from '@sections/Clients';
import Thesis from '@sections/Thesis';
import Hero from '@common/heroImage';
import CTA from '@sections/CTA';
import Wave from '@utils/wave';
import { Container } from '@styles/global';

import styled from 'styled-components';

const Text = styled(Container)`
    position: absolute;
    left: 0;
    top: 0;
    bottom: 0;
    right: 0;
    opacity: 1;

    width: 100%;
    height: 70vh;
    display: flex;
    flex-flow: column;
    justify-content: center;
    align-items: center;
    text-align: center;

    @media (min-width: ${props => props.theme.screen.sm}) {
        height: 50vh;
    }

    h2 {
        color: var(--accent-color);
        font-size: 1.5rem;
        text-transform: uppercase;
        letter-spacing: 1.5px;
        font-weight: 800;

        @media (min-width: ${props => props.theme.screen.sm}) {
            font-size: 3rem;
        }
    }

    p {
        color: var(--accent-color);
    }

    .button {
        border-radius: 35px;
    }
`;

export default () => (
    <Layout>
        <Navbar accent="inverse" fluid />
        <Hero fileName="LA.jpg">
            <Text>
                <h2>Venture Capital</h2>
                <p>
                    We invest in exceptional entrepreneurs with groundbreaking
                    ideas, ready for international expansion
                </p>
                <Link to="/contact/">
                    <button className="button small">See Our Portfolio</button>
                </Link>
            </Text>
        </Hero>
        <Thesis />
        <Clients />
        <Wave accent="light" />
        <Team />
        <CTA>
            <h2>Contact</h2>
            <p>
                Are you an early stage company looking for funding or any
                business in need of expert software development resources?
            </p>
            <Link to="/contact/">
                <button className="button">Get in touch</button>
            </Link>
        </CTA>
        <Wave />
        <Footer />
    </Layout>
);
